.about__container{
    grid-template-columns: repeat(3,1fr);
    align-items: center;
    /* column-gap: 4rem; */
}

.about__img{
   width: 350px;
   border-radius: 1.5rem;
   justify-self: right;
   grid-column: span 1;
   margin-bottom: 50px; 
}

.about__info{
    grid-template-columns: repeat(3,140px);
    gap:1rem;
    margin-bottom: var(--mb-2);
    justify-content: center;
}

.about__box{
    background-color: var(--container-color);
    border: 1px solid rgba(0,0,0,0.1);
    border-radius: 0.75rem;
    text-align: center;
    padding: 1rem 1.25rem;
    display:flex;
}

.inside__aboutbox{
    margin: auto auto;
}

.about__icon{
    font-size: 1.5rem;
    color: var(--title-color);
    margin-bottom: var(--mb-0-5);
}

.about__title{
    font-size: var(--small-font-size);
    font-weight: var(--font-medium);
}

.about__subtitle{
    font-size: var(--tiny-font-size);
    display:flex;
    flex-direction:column;
}

.about__description{
    padding: 0rem 2rem 0rem 2rem;
    margin-bottom: var(--mb-2-5);
    text-align: justify;
    width: auto;
}

.about__data{
    text-align: center; 
    max-width: 800px;
    margin: auto auto;
   grid-column: span 2;
 }


/*=============== BREAKPOINTS ===============*/
/* For large devices */
@media screen and (max-width: 992px) {
    .about__container{
        grid-template-columns: 1fr;
        /* row-gap: 2.5rem; */
    }

    .about__img{
        width: 220px;
        justify-self:center ;
    }
    
    .about__box{
        padding: 0.75rem 0.5rem;
    }

    .about__data{
       text-align: center; 
       margin: auto auto;
    }

    .about__info{
        justify-content: center;
    }

    .about__description{
        /* padding: 0rem 5rem; */
        margin-bottom: 2rem;
    }
  }
  
  /* For medium devices */
  @media screen and (max-width: 576px) {
    .about__info{
        grid-template-columns: repeat(3,1fr);
        padding-left: 2rem;
        padding-right: 2rem;
    }

  }
  
  /* For small devices */
  @media screen and (max-width: 350px) {
    .about__info{
        grid-template-columns: repeat(2,1fr);
    }

    .about__data{
        padding: 2rem;
    }
  }
  